const PostLink = props => (
  <li>
    {/* here we are passing data via a query string parameter (i.e. 'title') */}
    {/* <Link href={`/post?title=${props.title}`}>
      <a>{props.title}</a>
    </Link> */}
    {/* 'as' defines the URL to show */}
    {/* <Link href="/p/[id]" as={`/p/${props.id}`}>
      <a>{props.id}</a>
    </Link> */}
    <Link href="/p/[id]" as={`/p/${post.id}`}>
      <a>{post.title}</a>
    </Link>
  </li>
);
